<template>
  <el-card>
    <template #default>
      <div class="product-tabs">
        <div
          v-for="(item, index) in productLict"
          :key="item.group_name"
          :class="[
            'product-tab',
            'cursor-pointer',
            'text-center',
            `product-${index + 1}`,
            { active: item.group_name === activeTab }
          ]"
          @click="switchProduct(item.group_name)"
        >
          <span class="product-tab-icon mb-[8px] text-[24px]">{{ item.icon || '‚ö°' }}</span>
          <div class="product-tab-name font-semibold">{{ item.group_show_name }}</div>
        </div>
      </div>
      <el-divider border-style="dashed" />
      <div class="flex gap-[20px] text-[14px] items-center">
        <span>ÂÖ∂‰ªñÈÄâÈ°π:</span>
        <div class="flex gap-[12px] items-center">
          <span>Áî®Êà∑:</span>
          <el-select
            v-model="searchForm.user"
            placeholder="ËØ∑ÈÄâÊã©Áî®Êà∑"
            class="w-[200px]"
            clearable
            filterable
          >
            <el-option
              v-for="item in userList"
              :key="item.open_id"
              :label="item.name"
              :value="item.open_id"
            />
          </el-select>
        </div>
        <div class="flex gap-[12px] items-center">
          <span>Êó∂Èó¥ËåÉÂõ¥:</span>
          <el-date-picker
            v-model="searchForm.date"
            type="daterange"
            unlink-panels
            start-placeholder="ÂºÄÂßãÊó•Êúü"
            end-placeholder="Êà™Ê≠¢Êó•Êúü"
            :shortcuts="shortcuts"
            value-format="YYYY-MM-DD"
            :clearable="false"
            :disabledDate="disabledDate"
          />
        </div>
        <el-button type="primary" class="ml-auto" @click="refresh">Âà∑Êñ∞</el-button>
      </div>
    </template>
  </el-card>
  <template v-if="currentData">
    <el-card v-if="categoryData" v-loading="allLoading" :class="['mt-3', 'product-overview', `product-${currentData.id}`]">
      <template #default>
        <div class="flex items-center mb-[20px]">
          <div
            class="overview-icon size-[48px] rounded-[12px] flex items-center justify-center text-[24px] mr-[16px]"
          >
            {{ currentData.icon || '‚ö°' }}
          </div>
          <div class="overview-info">
            <h2 class="text-[20px] mb-[4px] text-[#2c3e50]">{{ currentData.group_show_name }}</h2>
            <p class="text-[#7f8c8d]">{{ currentData.description }}</p>
          </div>
        </div>
        <div class="overview-stats">
          <div
            v-for="item in categoryData.data"
            :key="item.type"
            class="overview-stat text-center rounded-[12px] bg-[#f8f9fa] p-[16px]"
          >
            <div class="overview-stat-value text-[24px] mb-[4px]">
			  <span v-if="item.event_unit === 'Áßí'">{{ formatSecondsToChar(item.count) }}</span>
			  <span v-else>{{ formatNumberWithCommas(item.count) }}</span>
            </div>
            <div class="overview-stat-label text-[12px] text-[#7f8c8d] font-[500]">
              {{ item.event_show_name }}
            </div>
          </div>
        </div>
      </template>
    </el-card>
    <TableData
      :data="personnalData"
	  :name="currentData.group_show_name"
      @refresh="getPersonData"
      :class="[`product-${currentData.id}`, 'mt-3']"
	  v-loading="personLoading"
    />
  </template>
</template>

<script setup>
import { computed, reactive, watch, ref } from 'vue'
import { categoryList, defalutPagination } from './constant.js'
import TableData from './TableData.vue'
import {
  getWholeDataNew as getWholeData,
  getPersonalDataNew as getPersonalData,
  getAllPersonal,
  getProduct
} from '@service/task.js'
import dayjs from 'dayjs'
import { ElMessage } from 'element-plus'
// import { useUserInfoStore } from '@store/userInfo'
import { formatNumberWithCommas, formatSecondsToChar } from '@utils/utils'

const activeTab = ref()
const productLict = ref([])
const currentData = computed(() => {
  return productLict.value.find((el) => el.group_name === activeTab.value)
})

// Êï∞ÊçÆÂå∫
// const userInfoStore = useUserInfoStore()
const today = dayjs(new Date()).format('YYYY-MM-DD')
const searchForm = reactive({
  category: [],
  user: '',
  date: [today, today]
})
const userList = ref([])
const shortcuts = [
  {
    text: '‰ªäÂ§©',
    value: () => {
      const end = new Date()
      const start = new Date()
      return [start, end]
    }
  },
  {
    text: '‰∏äÂë®',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      return [start, end]
    }
  },
  {
    text: '‰∏äÊúà',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
      return [start, end]
    }
  }
]
const categoryData = ref(null)
const personnalData = ref([])
const allLoading = ref(false)
const personLoading = ref(false)

const switchProduct = (id) => {
	if (allLoading.value || personLoading.value) {
		ElMessage.warning('ËØ∑ÂãøÈ¢ëÁπÅÊìç‰ΩúÔºÅ')
		return
	}
  activeTab.value = id
}
const disabledDate = (time) => {
  return time.getTime() > Date.now()
}
const refresh = (val) => {
    getAllData()
    getPersonData(val)
}

// Ëé∑ÂèñÊï¥‰ΩìÊï∞ÊçÆ
const getAllData = () => {
  allLoading.value = true
  getWholeData({
    start_date: searchForm.date[0] || today,
    end_date: searchForm.date[1] || today,
    group_list: activeTab.value
  })
    .then((res) => {
      if (res.code === 0) {
        console.log(res.data.list, 'res')
        const data = res.data.list || []
        categoryData.value = data[0] || {}
      } else {
        ElMessage.error(res.message || 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•')
      }
    })
    .finally(() => {
      allLoading.value = false
    })
}
const getPersonData = (pager = defalutPagination) => {
  personLoading.value = true
  getPersonalData({
    start_date: searchForm.date[0] || today,
    end_date: searchForm.date[1] || today,
    group_list: activeTab.value,
    open_id: searchForm.user,
    ...pager
  })
    .then((res) => {
      if (res.code === 0) {
        const data = res.data || {}
		console.log(data, '‰∏™‰∫∫Êï∞ÊçÆ')
        personnalData.value = data
      } else {
        ElMessage.error(res.message || 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•')
      }
    })
    .finally(() => {
      personLoading.value = false
    })
}
const getAllPersonData = () => {
  getAllPersonal({}).then((res) => {
    if (res.code === 0) {
      const data = res.data.list || []
      userList.value = data
    } else {
      ElMessage.error(res.message || 'Ëé∑Âèñ‰∫∫ÂëòÊï∞ÊçÆÂ§±Ë¥•')
    }
  })
}
getProduct().then((res) => {
  if (res.code === 0) {
    const data = res.data.list || []
    data.forEach((el, index) => {
      const item = categoryList.find((x) => x.name === el.group_show_name)
      el.id = index + 1
      if (item) {
        el.description = item.description
        el.icon = item.icon
      }
    })
    console.log(data, 111)
	if (data.length) {
		activeTab.value = data[0].group_name
	}
    productLict.value = data
  } else {
    ElMessage.error(res.message || 'Ëé∑ÂèñÂàÜÁªÑÊï∞ÊçÆÂ§±Ë¥•')
  }
})
getAllPersonData()
// ÁîüÂëΩÂë®ÊúüÂå∫
watch(
  () => searchForm.date,
  () => {
      getAllData()
      getPersonData()
  }
)

watch(
  () => searchForm.user,
  () => {
    getAllData()
    getPersonData()
  }
)

watch(activeTab, () => {
	// searchForm.user = userInfoStore?.userInfo?.open_id || ''
    getAllData()
    getPersonData()
})
</script>

<style lang="scss" scoped>
@use 'sass:list';
$colorArr: (
  #5470c6,
  #91cc75,
  #fac858,
  #ee6666,
  #73c0de,
  #3ba272,
  #fc8452,
  #9a60b4,
  #ea7ccc,
  #fd79a8,
  #e17055,
  #a29bfe,
  #55a3ff,
  #fdcb6e,
  #00b894
);

@each $colorItem in $colorArr {
  $index: list.index($colorArr, $colorItem);
  .product-#{$index} {
    --product-color: #{$colorItem};
  }
}
.product-tabs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 12px;
}
.product-tab {
  padding: 16px 20px;
  border-radius: 12px;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  background: #f8f9fa;
  position: relative;
  overflow: hidden;
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  }
  &.active {
    background: var(--product-color);
    color: white;
    border-color: var(--product-color);
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
    transition: left 0.5s;
  }
  &:hover::before {
    left: 100%;
  }
}
.product-overview {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  border-left: 4px solid var(--product-color);
}

.overview-icon {
  background: var(--product-color);
}

.overview-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
}

.overview-stat-value {
  color: var(--product-color);
  font-weight: 600;
}

:deep(.el-checkbox) {
  margin-right: 12px;
  .el-checkbox__input {
    display: none;
  }
  .el-checkbox__label {
    padding-left: 4px;
  }
}
.el-card {
  border-radius: 16px;
}
</style>
