<template>
  <el-card>
    <div class="mb-[10px] text-[18px] font-[600] text-[#2c3e50]">{{ name }} - Áî®Êà∑Êï∞ÊçÆ</div>
    <template v-if="tableData && tableData.length">
      <el-table
        :data="tableData"
        style="width: 100%; min-height: 300px"
        max-height="600px"
        @sort-change="handleSortChange"
      >
        <el-table-column
          v-for="item in tableColumn"
          :key="item.event_name"
          :prop="item.event_name"
          :label="item.event_show_name"
          :sort-orders="['descending', 'ascending', null]"
          :sortable="item.event_name === 'user_name' ? false : 'custom'"
        >
          <template #default="scope">
            <span v-if="item.event_unit === 'Áßí'" class="overview-stat-value">{{ formatSecondsToChar(scope.row[item.event_name] || 0) }}</span>
            <span :class="{'overview-stat-value': item.event_name !== 'user_name'}" v-else>{{ scope.row[item.event_name] || 0 }}</span>
          </template></el-table-column
        >
      </el-table>
      <el-pagination
        class="mt-3 justify-end"
        v-model:page-size="pagination.pagesize"
        v-model:current-page="pagination.page"
        :page-sizes="[15, 50, 100, 150]"
        background
        layout="sizes, prev, pager, next"
        :total="data.count"
      />
    </template>
    <el-empty v-else description="ÊöÇÊó†Êï∞ÊçÆ" />
  </el-card>
</template>
<script setup>
import { watch, ref, reactive } from 'vue'
import { defalutPagination } from './constant'
import { formatSecondsToChar } from '@utils/utils'
const props = defineProps({
  data: {
    type: Object,
    required: true
  },
  name: {
    type: String,
    required: true
  }
})

const emit = defineEmits(['refresh'])
const tableData = ref([])
const tableColumn = ref([])
const pagination = reactive({ ...defalutPagination })

if (props.data && props.data.columns) {
  tableColumn.value = props.data.columns
}

const handleSortChange = (value) => {
  console.log(value.prop)
  let order_method = 'desc'
  let order_event = value.prop
  if (value.order === 'ascending') {
    order_method = 'asc'
  } else if (value.order === 'descending') {
    order_method = 'desc'
  } else {
    order_method = ''
    order_event = ''
  }

  let params = {
    order_method,
    order_event
  }
  pagination.page = 1

  emit('refresh', {
    ...params,
    ...pagination
  })
}

watch(
  () => props.data,
  (val) => {
    tableData.value = []
    if (val && val.columns) {
      tableColumn.value = val.columns
    }
    tableData.value = val.data.map((el) => {
      const obj = {}
      el.forEach((e) => {
        obj[e.event_name] = e.data
      })
      return obj
    })
  }
)

watch(pagination, (val) => {
  emit('refresh', val)
})
</script>
<style lang="scss" scoped>
.overview-stat-value {
  color: var(--product-color);
  font-weight: 600;
}</style>
