<template>
    <el-tabs v-model="activeTab" v-loading="loading" @tab-click="handleTabClick">
      <el-tab-pane v-for="item in tabs" :key="item.id" :label="item.name" :name="item.id">
        <el-card class="mt-3 ml-2 mr-2">
          <template #default>
            <div class="flex gap-[20px] text-[14px] items-center">
              <span>ÊâÄÂ±ûÁ±ªÁõÆ:</span>
              <div class="flex gap-[12px]">
                <!-- <el-checkbox
                  v-model="selectAllCategory"
                  size="small"
                  label="ÂÖ®ÈÉ®"
                  border
                  @click="handleSelectAllCategory"
                /> -->
                <el-checkbox-group v-model="searchForm.category">
                  <el-checkbox
                    v-for="categoryItem in categoryList"
                    size="small"
                    :key="categoryItem.id"
                    :value="categoryItem.id"
                    :label="categoryItem.name"
                    border
                  />
                </el-checkbox-group>
              </div>
            </div>
            <el-divider border-style="dashed" />
            <div class="flex gap-[20px] text-[14px] items-center">
              <span>ÂÖ∂‰ªñÈÄâÈ°π:</span>
              <div class="flex gap-[12px] items-center" v-if="activeTab === 2">
                <span>Áî®Êà∑:</span>
                <el-select
                  v-model="searchForm.user"
                  placeholder="ËØ∑ÈÄâÊã©Áî®Êà∑"
                  class="w-[200px]"
                  clearable
				  filterable
                >
                  <el-option
                    v-for="item in userList"
                    :key="item.open_id"
                    :label="item.name"
                    :value="item.open_id"
                  />
                </el-select>
              </div>
              <div class="flex gap-[12px] items-center">
                <span>Êó∂Èó¥ËåÉÂõ¥:</span>
                <el-date-picker
                  v-model="searchForm.date"
                  type="daterange"
                  unlink-panels
                  start-placeholder="ÂºÄÂßãÊó•Êúü"
                  end-placeholder="Êà™Ê≠¢Êó•Êúü"
                  :shortcuts="shortcuts"
                  value-format="YYYY-MM-DD"
                  :clearable="false"
                  :disabledDate="disabledDate"
                />
              </div>
              <el-button type="primary" class="ml-auto" @click="refresh">Âà∑Êñ∞</el-button>
            </div>
          </template>
        </el-card>
        <template v-if="item.id === 1">
          <CategoryTypeCard
            :categoryTypeList="categoryTypeList"
            :data="categoryData"
            class="mt-3 ml-2 mr-2"
          />
        </template>
        <template v-if="item.id === 2">
          <TableData
            :categoryTypeList="categoryTypeList"
            :data="personnalData"
            :count="userList.length"
            @refresh="getPersonData"
            class="mt-3 ml-2 mr-2"
          />
        </template>
      </el-tab-pane>
    </el-tabs>
</template>

<script setup>
import { computed, reactive, watch, ref } from 'vue'
import { tabs, categoryList, defalutPagination } from './constant.js'
import CategoryTypeCard from './CategoryTypeCard.vue'
import TableData from './TableData.vue'
import { getWholeData, getPersonalData, getAllPersonal } from '@service/task.js'
import dayjs from 'dayjs'
import { ElMessage } from 'element-plus'
import { useUserInfoStore } from '@store/userInfo'

// Êï∞ÊçÆÂå∫
const userInfoStore = useUserInfoStore()
const activeTab = ref(1)
const today = dayjs(new Date()).format('YYYY-MM-DD')
const searchForm = reactive({
  category: [],
  user: userInfoStore?.userInfo?.open_id || '',
  date: [today, today]
})
const selectAllCategory = computed(() => {
  return searchForm.category.length === categoryList.length
})
const user = ref()
const userList = ref([])
const shortcuts = [
  {
    text: '‰ªäÂ§©',
    value: () => {
      const end = new Date()
      const start = new Date()
      return [start, end]
    }
  },
  {
    text: '‰∏äÂë®',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      return [start, end]
    }
  },
  {
    text: '‰∏äÊúà',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
      return [start, end]
    }
  }
]
const categoryData = ref(null)
const personnalData = ref([])
const loading = ref(false)
// ‰º†ÁªôÂ≠êÁªÑ‰ª∂ÁöÑ
const categoryTypeList = computed(() => {
  if (selectAllCategory.value) {
    return [...categoryList]
  } else {
	if (searchForm.category && searchForm.category.length) {
		return [...categoryList.filter((el) => searchForm.category.includes(el.id))]
	}
    return [...categoryList]
  }
})

// ÊñπÊ≥ïÂå∫
// const handleSelectAllCategory = () => {
//   selectAllCategory.value = true
//   if (selectAllCategory.value) {
//     searchForm.category = []
//   } else {
//     searchForm.category = categoryList.map(el => el.id)
//   }
// }
const disabledDate = (time) => {
  return time.getTime() > Date.now()
}
const handleTabClick = () => {
  user.value = null
}
const refresh = (val) => {
  if (activeTab.value === 1) {
    getAllData()
  } else {
    getPersonData(val)
  }
}

// Ëé∑ÂèñÊï¥‰ΩìÊï∞ÊçÆ
const getAllData = () => {
  loading.value = true
  getWholeData({
    start_date: searchForm.date[0] || today,
    end_date: searchForm.date[1] || today
  })
    .then((res) => {
      if (res.code === 0) {
        const data = res.data.list[0] || {}
        categoryData.value = {
          1: data.w_data,
          2: data.i_data,
          3: data.t_data,
          4: data.n_data,
          5: data.v_data,
          6: data.image_generator_data,
          7: data.sf_data,
          8: data.jd_aic_data
        }
      } else {
        ElMessage.error(res.message || 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•')
      }
    })
    .finally(() => {
      loading.value = false
    })
}
const getPersonData = (pager = defalutPagination) => {
  loading.value = true
  const type_list = searchForm.category && searchForm.category.length ? searchForm.category : categoryList.map(el => el.id)
  getPersonalData({
    start_date: searchForm.date[0] || today,
    end_date: searchForm.date[1] || today,
    type_list: JSON.stringify(type_list),
    open_id: searchForm.user,
    ...pager
  })
    .then((res) => {
      if (res.code === 0) {
        const data = res.data.list || []
        personnalData.value = data
      } else {
        ElMessage.error(res.message || 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•')
      }
    })
    .finally(() => {
      loading.value = false
    })
}
const getAllPersonData = () => {
  getAllPersonal({}).then((res) => {
    if (res.code === 0) {
      const data = res.data.list || []
      userList.value = data
    } else {
      ElMessage.error(res.message || 'Ëé∑Âèñ‰∫∫ÂëòÊï∞ÊçÆÂ§±Ë¥•')
    }
  })
}

getAllData()
getAllPersonData()
// ÁîüÂëΩÂë®ÊúüÂå∫
watch(
  () => searchForm.date,
  () => {
    if (activeTab.value === 1) {
      getAllData()
    } else if (activeTab.value === 2) {
      getPersonData()
    }
  }
)

watch(
  () => searchForm.user,
  () => {
    getPersonData()
  }
)

watch(
  () => searchForm.category,
  () => {
    if (activeTab.value === 2) {
      getPersonData()
    }
  }
)

watch(activeTab, () => {
  if (activeTab.value === 1) {
    getAllData()
  } else if (activeTab.value === 2) {
    getPersonData()
  }
})
</script>

<style lang="scss" scoped>
:deep(.el-checkbox) {
  margin-right: 12px;
  .el-checkbox__input {
    display: none;
  }
  .el-checkbox__label {
    padding-left: 4px;
  }
}
</style>
